import { Col, Pagination, Row, Switch, Table } from "antd";
import { useEffect, useState } from "react";
import { TableColumn } from "../../models/baseModel";
import { MyFormItemSelect } from "./Input";
import { Box } from "./MyBox";
import { MyButton } from "./MyButton";
import './UIStyle.css';
// pageInfo={pageInfo}
// setPageInfo={setPageInfo}
// column={column}
// dataSource={data}
// loading={isLoading}
// loadData={loadData}

// {title:"Title",dataIndex:"dataIndex",key:"key",render=()=>{})}
export default function TableComponent(props: any) {
    const [timer, setTimer] = useState(100);
    const { loadData } = props;
    useEffect(() => {
        const interval = setInterval(() => {
            setTimer(timer - 1);
            if (timer === 1) {
                setTimer(100);
                loadData();
            }
        }, 1000);

        return () => clearInterval(interval);
    }, [timer, setTimer, loadData]);

    function renderColumn() {
        const { Column } = Table;
        if (props.column === undefined) {
            return;
        }

        return props.column.map((data: TableColumn, index: number) => {
            return (
                <Column
                    key={data.key}
                    className={data.className}
                    width={data.width}
                    title={() => { return renderTitle(data, index); }}
                    dataIndex={data.dataIndex}
                    render={data.render} />
            );
        });
    }

    function renderTitle(data: any, index: any) {
        return (
            <div>
                <p>{data.title}</p>
            </div>
        );
    }

    function handleChangeTypeOrder(data: any) {
        console.log(data);
        props.orderBy.setDesc(data, props.setOrderBy);
    }

    function handleChangeValue(data: any) {
        props.orderBy.setColumn(data, props.setOrderBy);
    }

    function handleChangePage(page: number, pageSize: number) {
        props.pageInfo.handleChangePage(page, pageSize, props.setPageInfo);
    }

    function handleClickRefreshData() {
        props.loadData();
    }

    return (
        <div className="my_table">
            <Box>
                <Row gutter={16}>
                    <Col span={6}>
                        <MyButton
                            value={`Refresh data in ${timer}`}
                            onClick={handleClickRefreshData} />
                    </Col>
                    <Col span={12}>
                        <MyFormItemSelect
                            value={props.orderBy?.column}
                            onChange={handleChangeValue}
                            options={props.column} />
                    </Col>
                    <Col span={6}>
                        <Switch
                            disabled={props.orderBy?.column === ''}
                            checked={props.orderBy?.desc}
                            onChange={handleChangeTypeOrder}
                            checkedChildren="Tăng dần"
                            unCheckedChildren="Giảm dần" />
                    </Col>
                    <Col span={12}>
                        <Pagination
                            style={{ display: props.pageInfo.showPagination ? '' : 'none' }}
                            current={props.pageInfo.pageNumber}
                            disabled={props.loading}
                            onChange={handleChangePage}
                            pageSize={props.pageInfo.pageSize}
                            total={props.pageInfo.total} />
                    </Col>

                </Row>
            </Box>

            <Table
                rowClassName={props.rowClassName}
                style={{ "paddingTop": "10px" }}
                bordered
                loading={props.loading}
                pagination={false}
                dataSource={props.dataSource}
                size="small">
                {renderColumn()}
            </Table>
        </div>
    );
}