import { MyAxios } from "../helper/myaxios";
import { PaginationRequest } from "../models/baseModel";

export class TaskProjectManageService {
    static GetList(paginationRequest: PaginationRequest, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects?${paginationRequest.generateQueryString()}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetDetail(key: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${key}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Search(q: string, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/query?q=${q}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Add(body: any, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects`;
        console.log(body);

        return MyAxios.Post(url, body, then, exp, final);
    }

    static Edit(key: string | undefined, body: any, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${key}`;
        return MyAxios.Put(url, body, then, exp, final);
    }

    static Delete(key: string | undefined, timestamp: string, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${key}?timestamp=${encodeURIComponent(timestamp)}`;
        return MyAxios.Delete(url, then, exp, final);
    }
}

export class TaskSprintManageService {
    static GetList(projectKey: string | undefined, paginationRequest: PaginationRequest | undefined, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/sprints${paginationRequest === undefined ? "" : "?" + paginationRequest?.generateQueryString()}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetDetail(projectKey: string | undefined, sprintKey: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/sprints/${sprintKey}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Add(projectKey: string | undefined, body: any, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/sprints`;
        return MyAxios.Post(url, body, then, exp, final);
    }

    static Edit(projectKey: string | undefined, sprintKey: string | undefined, body: any, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/sprints/${sprintKey}`;
        return MyAxios.Put(url, body, then, exp, final);
    }

    static Delete(projectKey: string | undefined, sprintKey: string | undefined, timestamp: string, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/sprints/${sprintKey}?timestamp=${encodeURIComponent(timestamp)}`;
        return MyAxios.Delete(url, then, exp, final);
    }
}